@use "../utils" as u;

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: linear-gradient(var(--darker-color), var(--lighter-color) 61%);
  color: var(--text-color);
  font-family: u.$Garamond;
}
